import React from 'react'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { getSession } from '../redux/slices/sessionSlice'
import { getUser } from '../redux/slices/userSlice'
import { BeakerIcon, PersonIcon, TerminalIcon } from '@primer/octicons-react'


// Import utils
import { logout } from '../utils/authentication'

// Import reactstrap components
import {
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
    UncontrolledDropdown,
    NavItem,
    NavLink,
    Nav,
  } from "reactstrap";

const AuthUserNavBar = () => {

    const sessionInfo = useSelector(getSession)
    const userInfo = useSelector(getUser)
    const dispatch = useDispatch()

    return(
        <Nav navbar>
            <NavItem>
                <NavLink tag={ Link } to="/labs">{ <BeakerIcon/> } Labs</NavLink>
            </NavItem>
            { sessionInfo.isLabActive && <NavItem><NavLink tag={ Link } to="/sandbox">{ <TerminalIcon/> } Sandbox</NavLink></NavItem>}
            <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                    <PersonIcon/>
                </DropdownToggle>
                <DropdownMenu right>
                    <DropdownItem tag={ Link } to="/userdetails">
                        User preferences
                    </DropdownItem>
                    {userInfo.userAdmin ? (<>
                    <DropdownItem divider />
                    <DropdownItem tag={ Link } disabled to="/" >Admin Panel</DropdownItem> 
                    <DropdownItem tag={ Link } to="/usersmgmt">User Management</DropdownItem> 
                    <DropdownItem tag={ Link } to="/groupsmgmt">Groups Management</DropdownItem> 
                    </>)
                    : null
                }
                 <DropdownItem divider />
                    <DropdownItem onClick={ logout(dispatch) }>
                        Logout
                    </DropdownItem>
                </DropdownMenu>
            </UncontrolledDropdown>
        </Nav>
    )
}

export default AuthUserNavBar
